.navbar {
    display: flex;
    z-index: 1000;
    justify-content: space-around;
    padding: 16px;
    box-shadow: 0 1px 3px -2px black;
    /* background-image: linear-gradient(to right, #ff5622, #ff9800); */
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    transition: background-image 0.3s ease, background-color 0.3s ease;
    
  }
  
  .navbar.scrolled {
    background-image: none;
    background-color: pink; /* Add a solid color or adjust as needed */
  }
  









.navbar{
display: flex;
justify-content: space-around;
padding: 16px;
box-shadow: 0 1px 3px -2px black;
/* background-image: linear-gradient(to right, #ff562277, #ff9800); */
position: fixed;
width: 100%;
top: 0;
left: 0;
right: 0;

}
.nav-logo{
display: flex;
align-items: center;
gap: 10px;
}
.nav-logo p{
    color: #171717;
    font-size: 38px;
    font-weight: 600;
}
.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px ;
    color: #626262;
    font-size: 20px;
    font-weight: 500;
}
.nav-menu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
}
.nav-menu hr{
    border: none;
    width: 80% ;
    height: 3px;
    border-radius: 10px;
    background:#ff4141;
}
.nav-login-cart{
    display: flex;
    align-items: center;
    gap: 45px;
}
.nav-login-cart button{
    width: 157px;
    height:58px ;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color: #515151;
    font-size: 20px;
    font-weight: 500;
    background: white;
    cursor: pointer;
}
.nav-login-cart button:active{
   background-color: #f3f3f3; 
}
.nav-cart-count{
width: 22px;
height: 22px;
display: flex;
justify-content: center;
align-items: center;
margin-top: -35px;
margin-left: -55px;
border-radius: 11px;
font-size: 14px;
background: red;
color: white;
} 